import {
  FETCH_ARTICLES_LIST,
  RECEIVE_ARTICLES_LIST,
  RECEIVE_ARTICLES_LIST_ERROR
} from "../actions";

const articlesList = (
  state = {
    isFetching: false,
    articles: [],
    error: ""
  },
  action
) => {
  switch (action.type) {
    case FETCH_ARTICLES_LIST:
      return {
        ...state,
        isFetching: true
      };
    case RECEIVE_ARTICLES_LIST:
      return {
        ...state,
        articles: action.articles,
        isFetching: false
      };
    case RECEIVE_ARTICLES_LIST_ERROR:
      return {
        ...state,
        articles: [],
        isFetching: false,
        error: action.error
      };
    default:
      return state;
  }
};

export default articlesList;
